<template>
  <div id="taskAdd">
    <Modal
      v-model="reproductionBuild"
      title="新建地图图层"
      @on-cancel="reset"
    >
      <Form ref="add" :model="AddData" :rules="ruleValidate" :label-width="90" style="padding:0 50px;">
        <FormItem prop="name" label="图层名称：">
          <Input v-model.trim="AddData.name" placeholder="请输入图层名称" size="default"/>
        </FormItem>
        <FormItem prop="type" label="服务类型：">
          <Select v-model="AddData.type">
            <Option :value="0">影像REST服务</Option>
            <Option :value="1">矢量REST服务</Option>
            <Option :value="2">影像瓦片REST服务</Option>
            <Option :value="3">WMS</Option>
            <Option :value="4">WMTS</Option>
          </Select>
        </FormItem>
        <FormItem prop="url" label="访问地址：">
          <Input v-model.trim="AddData.url" placeholder="请输入访问地址" size="default"/>
        </FormItem>
        <FormItem prop="typeId" label="图层类型：" >
          <Cascader :data="layerData" v-model="AddData.typeId" size="default" change-on-select></Cascader>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import {
  addDitu
} from '@/api/acquisitionTable'
export default {
  name: 'TaskAdd',
  data () {
    return {
      /* 添加数据表单参数 */
      AddData: {
        name: '',
        url: '',
        type: 0,
        createBy: 12,
        typeId: [] // 图层类型
      },
      /* 添加数据表单校验 */
      ruleValidate: {
        name: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        url: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        typeId: [
          { required: true, message: '任务名称必须填写', trigger: 'blur', type: 'array' }
        ]
      },
      /* 新建框 */
      reproductionBuild: false
    }
  },
  props: {
    /* 图层类型数据 */
    layerData: {}
  },
  methods: {
    /* 添加地图图层 */
    addSubmit () {
      this.$refs.add.validate(valid => {
        if (valid) {
          addDitu({
            data: {
              name: this.AddData.name,
              url: this.AddData.url,
              type: this.AddData.type,
              typeId: Number(this.AddData.typeId[this.AddData.typeId.length - 1]),
              createBy: this.AddData.createBy
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$emit('getTableData')
              this.$Notice.success({ title: '创建图层成功' })
              this.reproductionBuild = false
              this.AddData = {
                name: '',
                url: '',
                typeId: [],
                createBy: 12
              }
              this.reset()
            } else {
              this.$Message.info(res.data.msg)
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.AddData = {
        name: '',
        url: '',
        type: 0,
        typeId: [],
        createBy: 12
      }
      this.$refs.add.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
  .core{
    max-height: 200px;
    border: 1px solid #979797;
    border-radius: 5px;
    padding: 10px;
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
    .abs-zone{
      position: absolute;
      width: 100%;
      height: 100%;
      top:0;
      left: 0;
      background: rgba(255,255,255,.8);
      z-index: 1;
      .box{
        position:absolute;
        width: 100%;
        top:50%;
        left: 0;
        margin-top: -32px;
        text-align: center;
      }
    }
  }
</style>
